<template>
	<section id="signin" class=" d-flex align-items-center">

    <div class="container">
      <div class="row">
        <div class="col-lg-7 d-flex flex-column justify-content-center">
          <div class="signin-lgo">
         <img src="../assets/images/workwire.png">
       </div>


        </div>

           <div class="col-lg-5 d-flex  justify-content-center align-items-center pt-4 ">

           <div class="signin-box_b">
            <div class="sub-text">
            <h4> Sign in</h4>
           <!--  <h5> Choose a display name</h5> -->
            <div class="join-workshire_b">
              <form v-on:submit.prevent="LoginUser" method="post" class="body">
                 <div class="form-group">
            <input type="email" name="" class="form-control" placeholder="Email adresse" v-model="post.email" required>
             <p v-if="msg">{{ msg }}</p>
             </div>
            

            <div class="box-join_b">
            <button  type="submit" class="join-signin_b">Countinue <img src="../assets/images/Group.png"></button>
            
         </div>
        </form>
         <div class="or-line">
          <p> OR</p>

         </div>
   
</div>
<div class="all-social">
  <div>
<div>

<a href="#" class="alradt-acount_b"> <img src="../assets/images/Facebook-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>
<div>
<a href="#" class="alradt-acount_c"> <img src="../assets/images/Google-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>
<div>
<a href="#" class="alradt-acount_d"> <img src="../assets/images/Apple-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>

<div>
  <router-link to="/register-form" class="alradt-acount-inn ">
     <label>New user ?   </label> <span>Create an account</span> <img src="../assets/images/back-arow.png">
  </router-link>
  

</div>
</div>
</div>

<a href="#" class="alradt-inn"><span>Get help signing in </span>  </a>


</div>


           </div>


            </div>
          
           </div>


        <!-- <div class="col-lg-6 order-1 order-lg-2 hero-img" >
          <img src="image/sliderbg.png" class="img-fluid " alt="">
        </div> -->
      </div>
    </div>

  </section>


</template>
<script>
import { postRequest } from './ApiFunctions/index';

export default {
  name: "SigninForm",
  data() {
        return {
            errorMessage: '',
            post: {
                email: '',
            },
            msg: '',
        }
    },
    
    mounted(){
        console.log("$hostname",this.$hostname);
        if(this.$hostname){
            this.$router.push('/');
        }
    },
    methods: {
        async LoginUser() {
            
            
              var email_check = await postRequest('/check-email/',this.post);
              if(email_check.error){
                this.$swal('Email not found');
              }else{
                localStorage.setItem("email",this.post.email);
              
                this.$router.push({path:'/signin-form-step2'});
              }
        },
        validateEmail(value) {
            //eslint-disable-next-line

            if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value)) {
               return true;
            } else {
                return false;
            }
        }
    }
}
</script>


